import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SigninComponent } from './pages/signin/signin.component';
import { SigningoogleComponent } from './pages/signingoogle/signingoogle.component';
import { SigninupdateComponent } from './pages/signinupdate/signinupdate.component';
import { SignupComponent } from './pages/signup/signup.component';

const routes: Routes = [
  { path: '', redirectTo:'/signin', pathMatch:'full' },
  { path: 'signin', component: SigninComponent },
  { path: 'signinupdate/:ref', component: SigninupdateComponent },
  { path: 'signingoogle', component: SigningoogleComponent },
  { path: 'signup/:ref', component: SignupComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
