import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
import { NgForm } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
  //declaring variables to use
  error : any;
  account: any;
  Ref: any;
  email: string;
  password: string;
  

  constructor( public _data: AuthService, public _route: ActivatedRoute) { }

  //update student
  SignupUsers(userDetails: NgForm){
    this._data.createAcc(userDetails.value, userDetails.value.name);

  }

  /*SignInWithGoogle(){
    this._route.loginGoogle();
  }*/

  ngOnInit(): void {
    
    this._data.Register(this.email, this.password);
      this.email= this.password = '';

     //display data on browser
     this._data.getAcc().snapshotChanges().subscribe( action =>{
      console.log(action);
      this.account=action;
    })
  }

}
