import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';
//import { AgularFire,AuthProvider,AuthMethods } from '@angular/fire';
import { NgForm } from '@angular/forms';
import { from } from 'rxjs';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {
  //declare a variable
  persons: any;
  email :string;
  password: string; 

  constructor( public _data: AuthService ) { }

  
    SigninUsers(userData: NgForm){
      //add this to our database
       this._data.Adduser(userData.value, userData.value.name);
    }

    //SignInWithGoogle(){
   //   this._data.loginGoogle();
   // }

    ngOnInit() {

      //display data on browser
      this._data.signin(this.email,this.password);
      this._data.getUsers().snapshotChanges().subscribe( action =>{
        console.log(action);
        this.persons=action;
      })
  
    }

}
